import { Injectable, } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';

interface saveDraftParams {
  ConversationID: number | string,
  MessageID: number | string,
  Sender: number,
  RecipientList: any,
  subject: string,
  text: string
}

@Injectable({
  providedIn: 'root'
})

export class SaveDraftService {

  //当前对话ID
  converId: number = null;
  //当前信息ID
  msgId: number = null;
  //倒计时时间(秒) 1s = 1000
  timeOutSec: number = 10000;
  //是否执行任务开关
  switch: boolean = false;
  //修改数据时间
  changeTime: any = null;
  //当前修改数据内容
  changeData: any = null;

  constructor(protected Dash: DashboardService) { }

  //保存草稿
  saveDraft(params: saveDraftParams) {
    this.changeData = params;
    if (this.switch) return;
    this.switch = true;
    setTimeout(() => {
      console.log(this.changeData);
      this.Dash.POST(this.Dash.config.updateMsgDraft, this.changeData).then(result => {
        this.switch = false;
        this.changeTime = result;
        // console.log(result);
      });
    }, this.timeOutSec);
  }

}
