import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';

@Component({
  selector: 'app-appointment',
  templateUrl: './appointment.component.html',
  styleUrls: ['./appointment.component.css']
})
export class AppointmentComponent implements OnInit {

  //预约列表
  appiontment_list = [];
  //预约类型列表
  typeList: any = [];
  //联系人列表
  recipientsList: any = {
    student: [],
    advisor: [],
    connection: []
  };

  //选中联系人列表
  checkRecipients: any = {
    student: [],
    advisor: [],
    connection: []
  };
  //无信息浏览
  showNoneMessage: boolean = true;

  //是否筛选计划中的
  scheduledFlag: boolean = true;
  //是否筛选草稿
  draftFlag: boolean = true;
  //是否筛选取消的
  canceledFlag: boolean = true;

  constructor(protected Dash: DashboardService) { }

  ngOnInit() {
    this.getAppointments();
    if (location.pathname != "/appointment") {
      this.showNoneMessage = false;
    }
  }

  //获取所有预约
  getAppointments() {
    let params = {
      scheduled: this.scheduledFlag ? 1 : 0,
      draft: this.draftFlag ? 1 : 0,
      canceled: this.canceledFlag ? 1 : 0
    };
    this.Dash.GET(this.Dash.config.getAppList, params).then(result => {
      this.appiontment_list = result;
    });
  }

  //获取类型列表
  getTypeList() {
    this.Dash.GET(this.Dash.config.getAppTypeList).then(result => {
      this.typeList = result;
    });
  }

  //获取收件人列表
  getRecipientList() {
    this.Dash.GET(this.Dash.config.getAssRecipients).then(result => {
      this.recipientsList = result;
    });
  }

  //删除预约草稿
  removeAppDraft(id) {
    this.Dash.POST(this.Dash.config.removeAppDraft, {
      AppointmentID: id
    }).then(result => {
      if(result) console.log("删除成功!");
    });
  }

  checkboxChange(event, type) {
    if (type == "scheduled") {
      this.scheduledFlag = event.checked;
    }
    if (type == "draft") {
      this.draftFlag = event.checked;
    }
    if (type == "canceled") {
      this.canceledFlag = event.checked;
    }
    this.getAppointments();
  }

}
